import Button from "../../elem/Button";
import styled from "styled-components";
import { __addTodos } from "../../redux/modules/todos";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import useInputs from "../../hooks/useInputs";
import { memo } from "react";
function TodoForm() {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const { inputs, onChange, reset } = useInputs({
    title: "",
    content: "",
    color: "",
  });

  const onSubmit = (e) => {
    e.preventDefault();
    if (
      inputs.title.trim() === "" ||
      inputs.content.trim() === "" ||
      inputs.color.trim() === ""
    ) {
      return alert("  .");
    }
    dispatch(
      __addTodos({
        ...inputs,
        id: Date.now(),
        isDone: false,
        createdAt: Date.now(),
      })
    );
    reset();
    navigate("/todoList");
  };

  return (
    <StForm as="form" onSubmit={onSubmit}>
      <StBox>
        <StText></StText>
        <Input
          type="text"
          placeholder="  . (10)"
          minLength="3"
          maxLength="10"
          value={inputs.title}
          name="title"
          onChange={onChange}
        />
        <StText></StText>
        <Textarea
          type="text"
          placeholder="  . (200)"
          rows="5"
          maxLength="200"
          value={inputs.content}
          name="content"
          onChange={onChange}
        />
        <StText> </StText>
        <Select name="color" value={inputs.color} onChange={onChange}>
          <option disabled value="">
             
          </option>
          <option value="#F4CEB8">orange</option>
          <option value="#E4EF93">green</option>
          <option value="#EEABE0">pink</option>
          <option value="#D4B6F4">purple</option>
          <option value="#99B7FF">blue</option>
        </Select>
      </StBox>
      <Button></Button>
    </StForm>
  );
}

/* */
const StText = styled.div`
  font-size: 25px;
  font-weight: 500;
  margin: 10px 0px 10px 0px;
`;

/*  */
const StForm = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
`;

/* */
const Input = styled.input`
  width: 100%;
  height: 40px;
  border: 2px solid #40424454;
  border-radius: 10px;
  font-size: 16px;
  padding: 5px 10px;
`;

/*  */
const Textarea = styled.textarea`
  width: 100%;
  border: 2px solid #40424454;
  padding: 12px;
  font-size: 16px;
  border-radius: 10px;
  resize: none;
  &:focus {
    outline: none;
  }
`;

/* */
const StBox = styled.div`
  width: 100%;
`;

/**/
const Select = styled.select`
  width: 100%;
  height: 40px;
  font-size: 16px;
  border: 2px solid #40424454;
  border-radius: 10px;
`;
export default memo(TodoForm);
