/**        */
function timeCheck(prevTime = 1665846659220) {
  const currentTime = Date.now();
  const gap = (currentTime - prevTime) / 1000 / 60;
  if (gap <= 1) return "1 ";
  else if (1 < gap && gap < 60) return `${gap | 0} `;
  else if (60 <= gap && gap < 1440) return `${parseInt(gap / 60)} `;
  else if (1440 <= gap && gap < 2880) return " ";
  else if (2880 < gap && gap < 43200) return `${parseInt(gap / 60 / 30)} `;
  return gap;
}

export default timeCheck;
